// import logo from '..src/assets/images/logo5HF.png'
import React from 'react'
import { makeStyles } from '@material-ui/core/styles';
import AppBar from '@material-ui/core/AppBar';
import Paper from '@material-ui/core/Paper';
import Toolbar from '@material-ui/core/Toolbar';
import Button from '@material-ui/core/Button';

const drawerWidth = 275;

const useStyles = makeStyles((theme) => ({
    root: {
        display: 'flex',
    },
    appBar: {
        background: '#ffffcc',
        zIndex: theme.zIndex.drawer + 1,
        // zIndex: 1400
    },
    logoPaper: {
        background: '#ffffcc',
        flexGrow: 1,
    },
    drawer: {
        width: drawerWidth,
        // flexShrink: 0,
    },
    drawerPaper: {

        width: drawerWidth,
    },
    drawerContainer: {
        overflow: 'auto',
    },
    content: {
        // display: 'flex',
        padding: theme.spacing(2),
    },
    buttons: {
        variant: 'contained',
        background: '#cc6600',
}
}
));
const Header = () => {
const classes = useStyles();
    return (
<AppBar position="fixed" className={classes.appBar}>
                <Toolbar>
                    <Paper variant="outlined" className={classes.logoPaper}>
                        {/* <img src={logo} alt="HulumFurniture-Logo" /> */}
                    </Paper>
                    <div >
                        <Button className={classes.buttons}> Membership </Button>
                        <Button className={classes.buttons}>Login</Button>
                        <Button className={classes.buttons}>Signup</Button>
                    </div>
                </Toolbar>
            </AppBar>
    )
}

export default Header
